.header {
    width: 100%;
    margin: 0 auto;
    background-color: #f3f3f3;
}
.headerContainer{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: start;
}
.headerContact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}
.headerIcon {
    width: 20px;
    color: black;
}
.logo {
    width: 150px;
}